import React from "react";
import Computer404 from "./images/404Computer.svg";
export default () => (
  <div>
    <img src={Computer404} alt=" Computer with 404 on it's screen" />
    <h1>NOT FOUND</h1>
    <p>You just hit a route that doesn't exist... the sadness.</p>
  </div>
);



